/* purgecss start ignore */

.lc-table {
  border-style: none;
  border-collapse: collapse;
  font-size: 14px;
  line-height: 1.5;
  width: 100%;
  border-spacing: 0;
}

.lc-table-wrapper {
    overflow-x: auto;
    margin-top: 24px;
    margin-bottom: 24px;
  }

.lc-table-footnote {
    font-size: 14px;
    line-height: 1.5;
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
    border-style: solid;
    border-top-width: 1px;
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-gray-lightest), var(--tw-border-opacity));
    padding-top: 24px;
    margin-bottom: 24px;
    margin-top: -24px;
  }

.lc-table thead th, .lc-table thead td, .lc-table tbody th, .lc-table tbody td, .lc-table tfoot th, .lc-table tfoot td {
      text-align: left;
    }

.lc-table thead th, .lc-table thead td, .lc-table tbody th, .lc-table tbody td, .lc-table tfoot th, .lc-table tfoot td {
      padding-left: 20px;
      padding-right: 20px;
    }

.lc-table thead th, .lc-table thead td, .lc-table tbody th, .lc-table tbody td, .lc-table tfoot th, .lc-table tfoot td {
      padding-top: 16px;
      padding-bottom: 16px;
    }

.lc-table thead th:last-child:not(:first-child), .lc-table thead td:last-child:not(:first-child), .lc-table tbody th:last-child:not(:first-child), .lc-table tbody td:last-child:not(:first-child), .lc-table tfoot th:last-child:not(:first-child), .lc-table tfoot td:last-child:not(:first-child) {
        text-align: right;
      }

@media screen and (min-width: 768px) {

.lc-table thead th,
    .lc-table thead td,
    .lc-table tbody th,
    .lc-table tbody td,
    .lc-table tfoot th,
    .lc-table tfoot td {
        padding-left: 24px;

        padding-right: 24px;

        padding-top: 20px;

        padding-bottom: 20px;
    }
      }

.lc-table thead th, .lc-table thead td, .lc-table tfoot th, .lc-table tfoot td {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
    }

.lc-table thead th, .lc-table thead td, .lc-table tfoot th, .lc-table tfoot td {
      font-weight: 400;
    }

.lc-table thead th, .lc-table thead td {
      padding-top: 0;
    }

.lc-table tbody tr:nth-child(odd) td, .lc-table tbody tr:nth-child(odd) th {
        --tw-bg-opacity: 1;
        background-color: rgba(var(--lc-color-gray-lightest), var(--tw-bg-opacity));
      }

/* purgecss end ignore */
